import React from "react";
import "./styles.css";
import { ReactTerminal } from "react-terminal";

export default function App() {
  const commands = {
    whoami: "TuringBot",
    turnonall: "Turning on All Macchines",
    turnoffall:
      "Are you sure, this command could damage systems! Write *Confirm*",
    cd: (directory) => `changed path to ${directory}`,
    confirm: `Spegnimento in corso...`,
    status: (number) => `The Macchine ${number} running perfectly`,
    status_help: "status 'number' Macchine status"
  };

  const welcomeMessage = (
    <span>
      available commands: {Object.keys(commands).join(", ")}
      <br />
    </span>
  );

  return (
    <div className="App">
      <ReactTerminal welcomeMessage={welcomeMessage} commands={commands} />
    </div>
  );
}
